// $bodyFont: 18px/1.5 serif;
$bodyFont: sans-serif;
$preFont: monospace;
$linkColor: #0000EE;
$visitedLinkColor: #551A8B;
$fontSize: 18px;

html {
    font-family: $bodyFont;
    font-size: 18px;
    line-height: 1.3;
}

dt {
    margin-top: 0.75em;
    margin-bottom: 0.25em;
}

img.me-pic {
    margin-left: 1rem;
    margin-bottom: 1rem;
    float: right;
}

a {
    color: $linkColor;
    &:visited {
        color: $visitedLinkColor;
    }
}

ul {
    li a.active {
        font-weight: bold;
        text-decoration: none;
    }
    
    &.nav li.nav-item a.nav-link {
        padding-left: 0;
    }
}

#footnotes a, .content p a, .content li a, .content span a {
    text-decoration: underline;
    &:hover {
        border: 0;
        text-decoration: none;
    }
}

body {
    color: black;
    max-width: 50rem;
    margin: 0px auto;
    padding: 0px 15px;
}

pre, code, tt {
    font-family: $preFont;
}

.content {
    h2#references + div.sectionbody {
        font-size: 85%;
    }

    p {
        -ms-hyphens: auto;
        -webkit-hyphens: auto;
        -moz-hyphens: auto;
        hyphens: auto;
    }

    li p {
        margin-bottom: 0.625rem;
    }
}

.hilink:target {
    background-color: #ffa;
}

pre.highlight {
    padding: 0.5rem 1rem;
    border-radius: 4px;
    color: #303030;
    font-size: 85%;
}

nav.navbar {
    font-family: sans-serif;

    ul {
        list-style-type: none;
        display: flex;
        justify-content: center;
        flex-direction: row;
        border-top: 1px solid #999;
        border-bottom: 1px solid #999;
        padding: 0.5rem 0 0.5rem 0px;
        // text-transform: uppercase;
        margin-top: -0.5rem;
        margin-bottom: 1.5rem;

        li {
            &.active {
                a {
                    font-weight: bold;
                    text-decoration: none;
                    color: #000 !important;                    
                }
            }
            
            padding: 0 1rem;
            a:link, a:visited, a:active {
                color: $linkColor;

                &:hover {
                    color: #333;
                }
            }
        }
    }
}

blockquote {
    padding: 0.5rem 2rem;
    font-style: italic;
}

/* kbd { */
/*     background-color: #ddd; */
/*     color: black; */
/*     border: 1px solid #bbb; */
/*     border-radius: 2px; */
/*     padding: 0 0.25rem; */
/* } */

footer {
    margin-bottom: 1em;
}

#footnotes, .footnotes {
    padding-top: 1rem;
    font-size: 90%;
    margin-bottom: 2rem;

    &:before {
        content: "Footnotes";
        font-variant: small-caps;
        color: #aaa;
        display: inline-block;
    }

    ol {
        margin-top: 1rem;
    }
}

.post-meta {
    color: #333;
    font-style: italic;
    margin-top: 0rem;
}

// asciidoctor crap

span.icon>.fa {
    cursor: default;
}

@media only screen and (min-width: 598px) {
    .quarter {
        width: 25%;
    }

    .third {
        width: 33%;
    }

    .half {
        width: 50%;
    }

    .twothirds {
        width: 66%;
    }

    .threequarters {
        width: 75%;
        align-items: center;
        margin: 1rem auto;
    }
}

.imageblock {
    margin: 1rem 0rem;


    .title {
        color: #666;
        font-size: 85%;
    }

    .content {

        img {
            height: auto;
            width: 100%;
        }
    }
}

.primary h1 > a.anchor, h2 > a.anchor, h3 > a.anchor, #toctitle > a.anchor, .sidebarblock > .content > .title > a.anchor, h4 > a.anchor, h5 > a.anchor, h6 > a.anchor {
    position: absolute;
    z-index: 1001;
    width: 1.5ex;
    margin-left: -1.5ex;
    display: block;
    text-decoration: none !important;
    visibility: hidden;
    text-align: center;
    font-weight: normal;
}

.primary h1:hover>a.anchor,.primary h1>a.anchor:hover,h2:hover>a.anchor,h2>a.anchor:hover,h3:hover>a.anchor,#toctitle:hover>a.anchor,.sidebarblock>.content>.title:hover>a.anchor,h3>a.anchor:hover,#toctitle>a.anchor:hover,.sidebarblock>.content>.title>a.anchor:hover,h4:hover>a.anchor,h4>a.anchor:hover,h5:hover>a.anchor,h5>a.anchor:hover,h6:hover>a.anchor,h6>a.anchor:hover {
    visibility:visible
}

.primary h1 > a.anchor::before, h2 > a.anchor::before, h3 > a.anchor::before, #toctitle > a.anchor::before, .sidebarblock > .content > .title > a.anchor::before, h4 > a.anchor::before, h5 > a.anchor::before, h6 > a.anchor::before {
    content: "\00A7";
    font-size: 0.85em;
    display: block;
    padding-top: 0.1em;
}

figure {
    &.float-sm-right { float: right; }
    &.w-25 { max-width: 25%; }
    &.w-50 { width: 50%; }
    &.w-75 { width: 75%; }
    
    img {
        max-width: 100%;
        height: auto;
        margin-bottom: 0.5rem;
        line-height: 1;
    }

    figcaption {
        font-size: 90%;
        color: rgb(108, 117, 125);
    }
}

code.highlighter-rouge, pre.highlight code, kbd {
    font-size: 1em;
}

hr {
    border: none;
    border-top: 1px solid #999;
    margin: 1rem 0rem;
}
